import { Injectable} from '@angular/core';
import { Recipe } from './recipes.model';
import { Ingredient } from '../shared/ingredient.model';
import { ShoppingListService } from '../shopping-list/shopping-list.service';
import { Subject } from 'rxjs';




@Injectable({
  providedIn: 'root'
})
export class RecipeService {

  // url = "http://localhost:3000/recipes";
  recipeChanged = new Subject<Recipe[]>();

  private recipes: Recipe[] = [
    new Recipe (
      'recipe title',
      'this is first recipe description',
      'https://assets.bonappetit.com/photos/5d7296eec4af4d0008ad1263/master/w_4500,h_2532,c_limit/Basically-Gojuchang-Chicken-Recipe-Wide.jpg',
      [
         new Ingredient('bans', 2),
         new Ingredient('meat', 5)
      ]),

    new Recipe (
      'another recipe title',
      'this is another recipe description',
      'https://images.kglobalservices.com/www.kelloggs.com/en_us/recipe/kicrecipe-1605/recip_img-7547527_rkt-original-treats.jpg',
      [
        new Ingredient('french fries', 5),
        new Ingredient('beef', 3)
      ])
  ];

  constructor( private slService: ShoppingListService) { }

  getRecipes() {
    return this.recipes.slice();
  }

  getRecipe(index: number) {
    return this.recipes[index];
  }

  addIngredientsToShoppingList(ingredient: Ingredient[]) {
    this.slService.addIngredients(ingredient);
  }

  addRecipe(recipe: Recipe) {
    this.recipes.push(recipe);
    this.recipeChanged.next(this.recipes.slice());
  }

  updateRecipe(index: number, newRecipe: Recipe) {
    this.recipes[index] = newRecipe;
    this.recipeChanged.next(this.recipes.slice());
  }

  deleteRecipe(index: number) {
    this.recipes.splice(index, 1);
    this.recipeChanged.next(this.recipes.slice());
  }

}
